<template>
  <div class="home">
    <p class="title landIn" ref="title">
      <!-- Ano hi watashitachi mada shiranai no Fushigi no monogatari desu. -->
      I am loser, but I never lost hope. Therefore, I have been working
      silently.
    </p>
    <div class="btn">dsadsad</div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      title: "Ano hi watashitachi mada shiranai no Fushigi no monogatari desu.",
    };
  },
  create() {},
  mounted() {
    this.textShowAnimation();
  },
  methods: {
    textShowAnimation() {
      let landInTexts = this.$refs.title;
      let letters = landInTexts.textContent.split("");
      landInTexts.textContent = "";
      letters.forEach((letter, i) => {
        let span = document.createElement("span");
        span.textContent = letter;
        span.className = "title_item";
        span.style.animationDelay = `${i * 0.05}s`;
        landInTexts.append(span);
      });
    },
  },
  components: {},
};
</script>

